/* src/components/ProfilePage.css */

.profile-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .profile-header {
    display: flex;
    align-items: center;
  }
  
  .profile-picture {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-right: 20px;
  }
  
  .profile-user-info {
    flex: 1;
  }
  
  .profile-username {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .profile-stats {
    display: flex;
    margin-bottom: 10px;
  }
  
  .profile-stats p {
    margin-right: 20px;
  }
  
  .profile-bio {
    font-size: 14px;
    color: #666;
  }
  